body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border: 10px solid slategray;
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  left: 0;
  text-align: center;
}

.buttons {
  bottom: 0%;
}

button:first-child {
  background: lightcoral;
  border-radius: 0px 0px 0px 10px;
}

button:nth-child(2) {
  background: lightcyan;
  font-size: 14px;
}

button:nth-child(3) {
  background: limegreen;
  border-radius: 0px 0px 10px 0px;
}

.buttons > * {
  font-size: 16px;
  width: 33.33%;
  height: 50px;
  border: none;
  cursor: pointer;
}

.number-container {
  width: 50%;
  min-width: 200px;
  border: 0.5px solid #d7d7d7;
  border-radius: 10px;
  margin: 0 auto;
}

.number-container > p {
  font-size: 36px;
  margin: 0;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: darkslategray;
}

.paragraph {
  width: 50%;
  min-width: 200px;
  margin: 0 auto;
  padding-bottom: 40px;
  line-height: 2;
  color: darkslategray;
}

.title {
  color: darkslategray;
  padding: 100px 0 20px 0;
}

@media only screen and (min-width: 450px) {
  body {
    border: 20px solid slategray;
  }

  .buttons > * {
    font-size: 18px;
  }
  button:nth-child(2) {
    font-size: 16px;
  }

  .number-container {
    width: 300px;
    border: 1px solid #d7d7d7;
    margin: 0 auto;
  }

  .number-container > p {
    font-size: 48px;
  }

  .paragraph {
    font-size: 20px;
    padding-bottom: 60px;
  }

  .title {
    color: darkslategray;
    padding: 120px 0 40px 0;
  }
}
